<script>
export default {
  name: "MyPersonal",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        oldPass: '',
        pass: '',
        checkPass: '',
      },
      rules: {
        oldPass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
      },
      lock:false,
      user:{
        name:"",
        avatar:'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
        sex:"",
        phone:"13766281021",
        email:"277282@qq.com",
        birth:"2010-10-1",
        city:"",
        school:"",
        major:"",
        education:"",
      },
      sexs:[{
        value: '',
        label: ''
      },{
        value: '',
        label: ''
      },],
      educations:[{
        value: '',
        label: ''
      },{
        value: '',
        label: ''
      },{
        value: '',
        label: ''
      }],
    };
  },
  methods:{
    save(){
      console.log(this.user)
      this.lock=!this.lock
    },
    active(){
      this.lock=!this.lock
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm);
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getUserInfo(){
      this.$http({
        url:"http://rap2api.taobao.org/app/mock/309060/user",
        method:'get',
      }).then(res=>{
        if (res.data.code=200){
          this.user=res.data.data.user
        }
      })
    },
  },
  created() {
    // this.getUserInfo()
  }
}
</script>

