                <el-form-item label=": *" prop="pass" >
                  <el-input type="password" v-model="ruleForm.pass" autocomplete="off" show-password></el-input>
                </el-form-item>
                <el-form-item label=": *" prop="checkPass">
                  <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off" show-password></el-input>
                </el-form-item>
              </div> <input type="button" name="next" class="next action-button" value="" />
            </fieldset>
            <fieldset>
              <div class="form-card">
                <div class="row">
                  <div class="col-7">
                    <h2 class="fs-title">:</h2>
                  </div>
                  <div class="col-5">
                    <h2 class="steps">Step 2 - 4</h2>
                  </div>
                </div>
                <el-form-item label=": *">
                  <el-input   type="text"  prop="name" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form :inline="true" label-position="top">
                <el-form-item :inline="true" label=" : *" prop="sex">
                  <el-radio-group v-model="ruleForm.sex">
                    <el-radio label="" border></el-radio>
                    <el-radio label="" border></el-radio>
                  </el-radio-group>
                </el-form-item>
                <el-form-item label=" : *" prop="sex">
                  <el-date-picker
                      v-model="ruleForm.pass"
                      type="date"
                      placeholder="">
                  </el-date-picker>
                </el-form-item>
                  <el-form-item label=" : *">
                  <el-input   type="text"  prop="name" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
                </el-form-item>
                  <el-form-item label="">
                    <el-select v-model="ruleForm.oldPass" placeholder="">
                      <el-option label="" value="shanghai"></el-option>
                      <el-option label="" value="beijing"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label=" : *">
                    <el-input   type="text"  prop="name" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item label=" : *">
                    <el-input   type="email"  prop="name" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <input type="button" name="next" class="next action-button" value="" />
              <input type="button" name="previous" class="previous action-button-previous" value="" />
            </fieldset>
            <fieldset>
              <div class="form-card">
                <div class="row">
                  <div class="col-7">
                    <h2 class="fs-title">:</h2>
                  </div>
                  <div class="col-5">
                    <h2 class="steps">Step 3 - 4</h2>
                  </div>
                </div>
                <el-form :inline="true" label-position="top">
                  <el-form-item label=" : *">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                      <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                  </el-form-item>
                  <el-form-item label=" : *">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                      <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                  </el-form-item>
                </el-form>
              </div>
              <input type="button" name="next" class="next action-button" value="" />
              <input type="button" name="previous" class="previous action-button-previous" value="" />
              <div style="height: 120px;background: #ffffff">

              </div>
            </fieldset>

            <fieldset>
              <div class="form-card">
                <div class="row">
                  <div class="col-7">
                    <h2 class="fs-title">:</h2>
                  </div>
                  <div class="col-5">
                    <h2 class="steps">Step 4 - 4</h2>
                  </div>
                </div> <br><br>
                <h2 class="purple-text text-center"><strong> !</strong></h2> <br>
                <br><br>
                <div class="row justify-content-center">
                  <div class="col-7 text-center" @click="toLogin()">
                    <a><h5 class="purple-text text-center">iCareer</h5></a>
                  </div>
                  <div style="height: 140px;background: #ffffff">

                  </div>
                </div>
              </div>
            </fieldset>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery.min.js"
export default {
  name: "Register",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        oldPass: '',
        pass: '',
        email:"",
        checkPass: '',
        sex:"",
      },
      rules: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
      }
    };
  },
  methods:{
    initPage() {
      $(document).ready(function(){

        var current_fs, next_fs, previous_fs; //fieldsets
        var opacity;
        var current = 1;
        var steps = $("fieldset").length;

        setProgressBar(current);

        $(".next").click(function(){

          current_fs = $(this).parent();
          next_fs = $(this).parent().next();

//Add Class Active
          $("#progressbar li").eq($("fieldset").index(next_fs)).addClass("active");

//show the next fieldset
          next_fs.show();
//hide the current fieldset with style
          current_fs.animate({opacity: 0}, {
            step: function(now) {
// for making fielset appear animation
              opacity = 1 - now;

              current_fs.css({
                'display': 'none',
                'position': 'relative'
              });
              next_fs.css({'opacity': opacity});
            },
            duration: 500
          });
          setProgressBar(++current);
        });

        $(".previous").click(function(){

          current_fs = $(this).parent();
          previous_fs = $(this).parent().prev();

//Remove class active
          $("#progressbar li").eq($("fieldset").index(current_fs)).removeClass("active");

//show the previous fieldset
          previous_fs.show();

//hide the current fieldset with style
          current_fs.animate({opacity: 0}, {
            step: function(now) {
// for making fielset appear animation
              opacity = 1 - now;

              current_fs.css({
                'display': 'none',
                'position': 'relative'
              });
              previous_fs.css({'opacity': opacity});
            },
            duration: 500
          });
          setProgressBar(--current);
        });

        function setProgressBar(curStep){
          var percent = parseFloat(100 / steps) * curStep;
          percent = percent.toFixed();
          $(".progress-bar")
              .css("width",percent+"%")
        }

        $(".submit").click(function(){
          return false;
        })

      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm);
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    toLogin(){
      this.$router.push("/login")
    }
  },mounted() {
    this.initPage();
  }
}
</script>

<style src="bootstrap/dist/css/bootstrap.css" scoped></style>
